@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;700&display=swap);
body {
  margin: 0;
  background-color: #f9f9f9;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: SourceSansPro-Regular;
}

button {
  cursor: pointer;
}

button:hover,
button:active {
  transform: scale(1.1);
}
button:focus {
  outline: thin dotted;
}

section.hero {
  background-image: url(/React_mock_rewards_store/static/media/header-x1.b6f067c6.png);
  height: 412px;

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  margin-bottom: 67px;
}

section.hero h1 {
  position: absolute;
  margin: 0;
  top: 284px;
  left: 132px;
  font-size: 64px;
  color: #ffffff;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  min-width: 400px;
  padding: 50px;
  z-index: 1000;
}

button.close-modal {
  font-size: 28px;
  font-weight: bold;
  line-height: 22px;
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px;
  color: #aaaaaa;
  border: none;
  background-color: transparent;
  transition: ease 0.1s;
}

button.close-modal:hover,
button.close-modal:focus {
  color: #000;
  cursor: pointer;
  outline: none;
}

button.close-modal:hover {
  transform: scale(1.2);
  color: #0ad4fa;
}

button.close-modal:focus {
  transform: scale(1.2);
}

.modal-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #616161;
  padding: 15px;
  max-height: calc(100vh - 210px);
  overflow-y: auto;
}

.modal-content button {
  background-color: #ededed;
  border: none;
  border-radius: 100px;
  width: 124px;
  height: 38px;
  cursor: pointer;

  font-size: 16px;
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: normal;
  color: #616161;
  letter-spacing: -0.15px;
}

/* Reset Select */
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #ededed;
  background-image: none;
}
/* Remove IE arrow */
select::-ms-expand {
  display: none;
}
/* Custom Select */
.select {
  position: relative;
  display: flex;
  width: 12em;
  height: 2em;
  line-height: 2;
  background: #616161;
  overflow: hidden;
  border-radius: 0.25em;

  margin-bottom: 24px;
}
select {
  flex: 1 1;
  padding: 0 3.8em;
  color: #616161;
  cursor: pointer;
  font-size: 18px;
}
/* Arrow */
.select::after {
  content: '\25BC';
  color: #616161;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  cursor: pointer;
  pointer-events: none;
  transition: 0.25s all ease;
}
/* Transition */
.select:hover::after {
  color: #0ad4fa;
}

.modal-content ul {
  padding: 0;
}

/* CONTAINER */

.container {
  width: 1176px;
  margin: auto;
  text-align: center;
}

/* CONTAINER -> FILTER  */
section.container .filter-grid {
  display: grid;
  grid-template-columns: 881px 1fr 48px 48px;
  grid-template-rows: 48px;
  grid-column-gap: 12px;
  -webkit-column-gap: 12px;
          column-gap: 12px;
  margin-bottom: 24px;
}

section.container .filter-area {
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style-type: none;
  padding: 0;
  color: #a3a3a3;
  font-size: 24px;
  letter-spacing: -0.15px;
  line-height: 48px;
}

section.container .filter-area li:first-child {
  color: #616161;
}

section.container .filter-area button {
  font-size: 18px;
  background: #ededed;
  display: block;
  color: inherit;
  border-radius: 100px;
  width: 172px;
  height: 48px;
  border: none;
  cursor: pointer;
}

section.container .filter-area button:active {
  background: #0ad4fa;
  color: #ffffff;
  outline: none;
}

section.container .filter-area button:focus {
  background: #0ad4fa;
  color: #ffffff;
  outline: none;
}

section.container li.filter-divider {
  margin: 0;
  background: #d9d9d9;
  width: 1px;
  height: 49px;
}

.horizontal-divider {
  margin: 0 0 30px 0;
  background: #d9d9d9;
  width: 1176px;
  height: 1px;
}

/* BUTTONS */
.btn.page-changer:focus {
  outline: none;
}

.btn.page-changer:active {
  -webkit-filter: brightness(0) saturate(100%) invert(82%) sepia(34%) saturate(7048%) hue-rotate(146deg)
    brightness(103%) contrast(96%);
          filter: brightness(0) saturate(100%) invert(82%) sepia(34%) saturate(7048%) hue-rotate(146deg)
    brightness(103%) contrast(96%);
}
.btn.page-changer.next {
  cursor: pointer;
  border: none;
  border-radius: 100%;
  background-color: transparent;
  background-image: url(/React_mock_rewards_store/static/media/arrow-right.3306c112.svg);
}

.btn.page-changer.prev {
  cursor: pointer;
  border: none;
  border-radius: 100%;
  background-color: transparent;
  background-image: url(/React_mock_rewards_store/static/media/arrow-left.e6eb9be1.svg);
}

.filter-grid .btn.page-changer.prev {
  grid-column-start: 3;
}

.filter-grid .btn.page-changer.next {
  grid-column-start: 4;
}

/* CARD GRIDS */

.card-grid {
  display: grid;
  grid-template: repeat(4, 279px) / repeat(4, 276px);
  grid-column-gap: 24px;
  -webkit-column-gap: 24px;
          column-gap: 24px;
  grid-row-gap: 24px;
  row-gap: 24px;
  margin-bottom: 60px;
}

section.container .foot.filter-area {
  justify-self: baseline;
}

.card-grid .card {
  position: relative;
  background: #ffffff;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  padding: 12px;

  display: flex;
  flex-direction: column;
  align-items: center;

  transition: 0.2s ease-out;
}

.card.fund:hover {
  transform: translateY(-12px);
  transition: 0.2s ease-in;
  background: linear-gradient(180deg, rgba(10, 212, 250, 0.8) 0%, rgba(37, 187, 241, 0.8) 100%);
  -webkit-filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.2));
          filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.2));
}

.card.fund:hover .small-horizontal-divider,
.card.fund:hover .product-info {
  opacity: 0.2;
}

.card.fund:hover .card-image img {
  opacity: 0.05;
}

/* Card Image */
.card .card-image {
  position: relative;
  width: 252px;
  height: 182px;
}

.card .card-image img {
  width: 100%;
}

/* Buy Button */
.card button.buy-button {
  width: 42px;
  height: 42px;
  border: none;
  padding: 0;

  position: absolute;
  transform: translate(-100%, 0);
  left: 100%;

  background: none;
  cursor: pointer;
}

.card button.buy-button:focus {
  outline: none;
}

.card button.buy-button svg {
  outline: none;
  transition: transform 0.15s;
}

.card button.buy-button svg:hover {
  transform: scale(1.1);
}

.card button.buy-button svg:active {
  transform: scale(1);
}

/* NoFunds Button */

.card button.no-funds {
  width: 142px;
  height: 42px;
  border: none;
  padding: 0;

  position: absolute;
  transform: translate(-100%, 0);
  left: 100%;

  mix-blend-mode: normal;
  opacity: 0.8;
  border-radius: 100px;
  background: #616161;
  cursor: pointer;

  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.0329412px;
  color: #ffffff;
}

.card button.no-funds > svg,
.card button.no-funds > span {
  vertical-align: middle;
}

/* Horizontal Divider */
.card-grid .card .small-horizontal-divider {
  background: #d9d9d9;
  width: 228px;
  height: 1px;
}

/* Product Info */
.card .product-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 228px;
  margin-top: 19px;
}

.product-info p {
  margin: 0;
}

.product-category {
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.0376471px;
  align-self: baseline;

  color: #a3a3a3;
}

.product-title {
  font-size: 18px;
  line-height: 23px;

  letter-spacing: -0.0423529px;

  color: #616161;
}

/* REDEEM PANEL */

.redeem-panel {
  position: absolute;
  top: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.redeem-panel .redeem-price {
  display: flex;
  align-items: center;
  margin: 15px;
}

.redeem-panel .redeem-price p {
  color: #ffffff;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  letter-spacing: -0.0847059px;
  margin: 0;
}

.redeem-panel .redeem-price svg {
  transform: translate(3px, 3px);
}

.redeem-panel button.redeem-button {
  font-size: 18px;
  line-height: 23px;
  letter-spacing: -0.0423529px;

  color: #616161;

  background: #eeeeee;
  border: none;
  border-radius: 22px;
  height: 42px;
  width: 229px;

  transition: transform 0.15s;
}

.redeem-panel button.redeem-button:focus {
  outline: none;
}

.redeem-panel button.redeem-button:active {
  transform: scale(0.9);
}

.redeem-panel button.redeem-button:hover {
  background: #e0dede;
}

header.head {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header.head .user {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 5px;
  -webkit-column-gap: 5px;
          column-gap: 5px;
  justify-content: space-between;
  align-items: center;
  margin-right: 12px;
}

header.head .user p {
  color: #616161;
  text-align: center;
  letter-spacing: -0.15px;
  font-size: 24px;
}

header.head .user .btn-header {
  border: none;
  border-radius: 100px;
  width: 103px;
  height: 48px;
  cursor: pointer;
  background-color: #ededed;

  font-size: 20px;
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: normal;
  color: #616161;
  letter-spacing: -0.15px;
}

header.head .user .point-info {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 16px;
}

header.head .user .point-info img {
  margin-left: 6px;
  transform: translateY(3px);
}

header.head .logo {
  margin-left: 42px;
}

